import React, { Dispatch, SetStateAction, useState } from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import { Task } from '../../components/atoms/task/Task';
import { Log } from '../../interfaces/log';
import { IRepository } from '../../interfaces/IRepository';


export function Tracker(props: { repo: IRepository }) {
    const { repo } = props
    const [taskName, setTaskName] = useState('')
    const [time, setTime] = useState(repo.getTime())
    const [currentTaskName, setCurrentTaskName] = useState(repo.getCurrentTaskName())
    const [isWorking, setIsWorking] = useState(repo.getIsWorking())
    const [history, setHistory]: [string[], Dispatch<SetStateAction<string[]>>] = useState(repo.getHistory())
    const [logs, setLogs]: [Log[], Dispatch<SetStateAction<Log[]>>] = useState(repo.getLogs)

    const handleChange_taskName = (event: React.ChangeEvent<HTMLInputElement>) => {
        setTaskName(event.target.value);
    };

    const act = (taskName: string): void => {
        setCurrentTaskName(taskName)
        repo.setCurrentTaskName(taskName)

        const time = Date.now()
        setTime(time)
        repo.setTime(time)

        setIsWorking(true)
        repo.setIsWorking(true)

        setTaskName('')
    }

    const addHistory = (taskName: string): void => {
        const newHistory = [taskName, ...history.filter(v => v !== taskName)]

        setHistory(newHistory)
        repo.setHistory(newHistory)
    }

    const switchTask = (taskName: string): void => {
        if (currentTaskName === taskName || taskName === '') { return setTaskName('') };

        if (isWorking) { stopTask() }

        act(taskName)

        addHistory(taskName)
    }

    const stopTask = (): void => {
        const stopTime = Date.now()
        const log: Log = {
            taskName: currentTaskName,
            start: time,
            end: stopTime,
            duration: stopTime - time,
        }
        const newLog = [log, ...logs]

        setLogs(newLog)
        repo.setLogs(newLog)

        setIsWorking(false)
        repo.setIsWorking(false)

        setCurrentTaskName('')
        repo.setCurrentTaskName('')
    }

    const handleClick_actButton = () => {
        switchTask(taskName)
    }
    const handleClick_playButton = (taskName: string) => {
        switchTask(taskName)
    }
    const handleClick_stopButton = () => {
        stopTask()
    }

    const formatLogs = (logs: Log[]): Log[][] => {
        if (logs.length === 0) return [[]];

        const ret: Log[][] = [[]]
        let i = 0
        let prev = new Date(logs[0].start).getHours()
        logs.forEach((log: Log) => {
            const start = new Date(log.start).getHours()
            if (prev === start) {
                ret[i].push(log)
            } else if (ret[i].length === 0) {
                ret[i].push(log)
                prev = start
            } else {
                i++
                ret[i] = []
                prev = start
            }
        })
        return ret
    }
    const formatDate = (logs: Log[]): string => {
        if (logs.length === 0) return '';
        const startHour: number = new Date(logs[0].start).getHours()
        const endHour: number = new Date(logs.slice(-1)[0].end).getHours()
        return `# ${startHour}:00 - ${endHour + 1}:00`;
    }
    return (
        <Box display="flex" className="Tracker">
            <Box display="flex" flexDirection="column">
                <Box display="flex">
                    Task: {currentTaskName}
                    {isWorking ? <Button onClick={handleClick_stopButton}><span className="material-icons">stop</span></Button> : void 0}
                </Box>
                <Box display="flex">
                    <TextField
                        id="standard-basic"
                        value={taskName}
                        onChange={handleChange_taskName}
                        label="new task" />
                    <Button onClick={handleClick_actButton}>act</Button>
                </Box>
                <Box display="flex" flexDirection="column">
                    {history.map(t => <Task taskName={t} handler={handleClick_playButton}></Task>)}
                </Box>
            </Box>
            <Box display="flex" flexDirection="column">
                <div>Logs</div>
                {formatLogs(logs).reverse().map(logs =>
                    <div>
                        <div>{formatDate(logs)}</div>
                        {logs.reverse().map(log => <div>- {log.taskName}</div>)}
                    </div>)}
            </Box>
        </Box>
    );
}
